import SectionLayout from "../Common/SectionLayout";
import ProjectItem from "./ProjectItem";

const Project = (props) => {
  const {openPage} = props;
  const projects = [
    {title:"헬스케어",date:"2021.05",content:"내용",img:"me.jpg",stack:["React","Spring","Redis"]},
    {title:"콩콩콩",date:"2021.06",content:"내용",img:"me.jpg",stack:["React","Spring","Redis"]},
  ];
  return (
    <SectionLayout>
      {
        projects.map((project) => {
          return(
            <ProjectItem project={project}/>
          );
        })
      }
    </SectionLayout>
  )

}
export default Project;